<template>
    <div class="suggestion-page">
        <div class="container">
            <!-- Ìó§Îçî ÏÑπÏÖò -->
            <div class="suggestion-header">
                <h1 class="page-title">Í±¥ÏùòÏÇ¨Ìï≠</h1>
                <p class="page-subtitle">ÏÜåÏ§ëÌïú ÏùòÍ≤¨ÏùÑ Îì§Î†§Ï£ºÏÑ∏Ïöî</p>
            </div>

            <!-- Í±¥ÏùòÏÇ¨Ìï≠ ÏûëÏÑ± Ìèº -->
            <div class="suggestion-form-container">
                <form @submit.prevent="submitSuggestion" class="suggestion-form">
                    <!-- Ïπ¥ÌÖåÍ≥†Î¶¨ -->
                    <div class="form-group">
                        <label for="category" class="form-label">Ïπ¥ÌÖåÍ≥†Î¶¨ <span class="required">*</span></label>
                        <select id="category" v-model="form.category" class="form-select" required>
                            <option value="">Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî</option>
                            <option value="feature">Í∏∞Îä• Ï†úÏïà</option>
                            <option value="bug">Î≤ÑÍ∑∏ Ïã†Í≥†</option>
                            <option value="improvement">Í∞úÏÑ† ÏÇ¨Ìï≠</option>
                            <option value="ui">UI/UX</option>
                            <option value="etc">Í∏∞ÌÉÄ</option>
                        </select>
                    </div>

                    <!-- Ï†úÎ™© -->
                    <div class="form-group">
                        <label for="title" class="form-label">Ï†úÎ™© <span class="required">*</span></label>
                        <input type="text" id="title" v-model="form.title" class="form-input"
                            placeholder="Í±¥ÏùòÏÇ¨Ìï≠ Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî" maxlength="100" required>
                        <div class="char-count">{{ form.title.length }}/100</div>
                    </div>

                    <!-- ÎÇ¥Ïö© (Vue ÎÑ§Ïù¥Ìã∞Î∏å ÏóêÎîîÌÑ∞) -->
                    <div class="form-group">
                        <label for="content" class="form-label">ÎÇ¥Ïö© <span class="required">*</span></label>
                        <div class="editor-container">
                            <!-- Ìà¥Î∞î -->
                            <div class="editor-toolbar">
                                <button type="button" @click="formatText('bold')"
                                    :class="{ active: isFormatActive('bold') }" class="toolbar-btn">
                                    <strong>B</strong>
                                </button>
                                <button type="button" @click="formatText('italic')"
                                    :class="{ active: isFormatActive('italic') }" class="toolbar-btn">
                                    <em>I</em>
                                </button>
                                <button type="button" @click="formatText('underline')"
                                    :class="{ active: isFormatActive('underline') }" class="toolbar-btn">
                                    <u>U</u>
                                </button>
                                <div class="toolbar-separator"></div>
                                <button type="button" @click="formatText('insertUnorderedList')" class="toolbar-btn">
                                    ‚Ä¢ Î™©Î°ù
                                </button>
                                <button type="button" @click="formatText('insertOrderedList')" class="toolbar-btn">
                                    1. Î™©Î°ù
                                </button>
                            </div>

                            <!-- ÏóêÎîîÌÑ∞ -->
                            <div ref="editor" class="rich-text-editor" contenteditable="true" @input="updateContent"
                                @keyup="updateContent" @paste="handlePaste" placeholder="Í±¥ÏùòÏÇ¨Ìï≠ ÎÇ¥Ïö©ÏùÑ ÏûêÏÑ∏Ìûà ÏûëÏÑ±Ìï¥Ï£ºÏÑ∏Ïöî..."></div>
                        </div>
                        <div class="form-help-with-counter">
                            <span>Íµ¨Ï≤¥Ï†ÅÏù∏ ÎÇ¥Ïö©ÏùÑ ÏûëÏÑ±Ìï¥Ï£ºÏãúÎ©¥ Îçî Îπ†Î•∏ Ï≤òÎ¶¨Í∞Ä Í∞ÄÎä•Ìï©ÎãàÎã§.</span>
                            <span class="char-count">{{ currentCharCount }}/500</span>
                        </div>
                    </div>


                    <!-- Ï≤®Î∂Ä Ïù¥ÎØ∏ÏßÄ -->
                    <div class="form-group">
                        <label for="images" class="form-label">Ï≤®Î∂Ä Ïù¥ÎØ∏ÏßÄ</label>
                        <div class="image-upload-area">
                            <input type="file" id="images" ref="fileInput" @change="handleFileUpload" multiple
                                accept="image/*" class="file-input">
                            <div class="upload-zone" @click="triggerFileInput" @drop="handleDrop" @dragover.prevent>
                                <div class="upload-icon">üì∑</div>
                                <div class="upload-text">
                                    <p>Ïù¥ÎØ∏ÏßÄÎ•º ÎìúÎûòÍ∑∏ÌïòÍ±∞ÎÇò ÌÅ¥Î¶≠ÌïòÏó¨ ÏóÖÎ°úÎìú</p>
                                    <small>PNG, JPG, GIF ÌååÏùº ÏßÄÏõê (ÏµúÎåÄ 5MB, ÏµúÎåÄ 3Í∞ú)</small>
                                </div>
                            </div>
                        </div>

                        <!-- ÏóÖÎ°úÎìúÎêú Ïù¥ÎØ∏ÏßÄ ÎØ∏Î¶¨Î≥¥Í∏∞ -->
                        <div v-if="uploadedImages.length > 0" class="image-preview-container">
                            <div class="image-preview-list">
                                <div v-for="(image, index) in uploadedImages" :key="index" class="image-preview-item">
                                    <img :src="image.url" :alt="image.name" class="preview-image">
                                    <button type="button" @click="removeImage(index)" class="remove-image-btn">
                                        √ó
                                    </button>
                                    <div class="image-name">{{ image.name }}</div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <!-- Ïó∞ÎùΩÏ≤ò (ÏÑ†ÌÉùÏÇ¨Ìï≠) -->
                    <div class="form-group">
                        <label for="contact" class="form-label">Ïó∞ÎùΩÏ≤ò (ÏÑ†ÌÉùÏÇ¨Ìï≠)</label>
                        <input type="text" id="contact" v-model="form.contact" class="form-input"
                            placeholder="ÎãµÎ≥ÄÏùÑ Î∞õÍ≥† Ïã∂ÏùÄ Ïù¥Î©îÏùºÏù¥ÎÇò Ïó∞ÎùΩÏ≤òÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî">
                        <div class="form-help">ÎãµÎ≥ÄÏù¥ ÌïÑÏöîÌïú Í≤ΩÏö∞ÏóêÎßå ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.</div>
                    </div>

                    <!-- Ï†úÏ∂ú Î≤ÑÌäº -->
                    <div class="form-actions">
                        <button type="button" @click="resetForm" class="btn btn-secondary">
                            Ï¥àÍ∏∞Ìôî
                        </button>
                        <button type="submit" class="btn btn-primary" :disabled="isSubmitting">
                            <span v-if="isSubmitting">Ï†úÏ∂ú Ï§ë...</span>
                            <span v-else>Í±¥ÏùòÏÇ¨Ìï≠ Ï†úÏ∂ú</span>
                        </button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Suggestion',
    computed: {
        currentCharCount() {
            return this.form.content ?
                (this.$refs.editor?.innerText?.length || 0) : 0;
        }
    },
    data() {
        return {
            form: {
                category: '',
                title: '',
                content: '',
                contact: ''
            },
            uploadedImages: [],
            isSubmitting: false
        }
    },
    methods: {
        updateContent() {
            // ÏàúÏàò ÌÖçÏä§Ìä∏ Í∏∏Ïù¥Îßå Ï≤¥ÌÅ¨
            const text = this.$refs.editor.innerText || '';
            if (text.length > 500) {
                // 500ÏûêÎ•º Ï¥àÍ≥ºÌïòÎ©¥ Ïù¥Ï†Ñ Í∞íÏúºÎ°ú Î≥µÏõê ÎòêÎäî ÏûêÎ•¥Í∏∞
                this.$refs.editor.innerText = text.slice(0, 500);
                // Ïª§ÏÑú Îß® ÎÅùÏúºÎ°ú Ïù¥Îèô
                document.execCommand('selectAll', false, null);
                document.getSelection().collapseToEnd();
            }
            this.form.content = this.$refs.editor.innerHTML;
        },

        formatText(command) {
            document.execCommand(command, false, null);
            this.$refs.editor.focus();
            this.updateContent();
        },

        isFormatActive(command) {
            return document.queryCommandState(command);
        },

        handlePaste(event) {
            event.preventDefault();
            const text = event.clipboardData.getData('text/plain');
            document.execCommand('insertText', false, text);
        },

        triggerFileInput() {
            this.$refs.fileInput.click();
        },

        handleFileUpload(event) {
            const files = event.target.files;
            this.processFiles(files);
        },

        handleDrop(event) {
            event.preventDefault();
            const files = event.dataTransfer.files;
            this.processFiles(files);
        },

        processFiles(files) {
            if (this.uploadedImages.length + files.length > 3) {
                alert('ÏµúÎåÄ 3Í∞úÏùò Ïù¥ÎØ∏ÏßÄÎßå ÏóÖÎ°úÎìú Í∞ÄÎä•Ìï©ÎãàÎã§.');
                return;
            }

            Array.from(files).forEach(file => {
                if (file.size > 5 * 1024 * 1024) {
                    alert(`${file.name}ÏùÄ(Îäî) 5MBÎ•º Ï¥àÍ≥ºÌï©ÎãàÎã§.`);
                    return;
                }

                if (!file.type.startsWith('image/')) {
                    alert(`${file.name}ÏùÄ(Îäî) Ïù¥ÎØ∏ÏßÄ ÌååÏùºÏù¥ ÏïÑÎãôÎãàÎã§.`);
                    return;
                }

                const reader = new FileReader();
                reader.onload = (e) => {
                    this.uploadedImages.push({
                        file: file,
                        url: e.target.result,
                        name: file.name
                    });
                };
                reader.readAsDataURL(file);
            });
        },

        removeImage(index) {
            this.uploadedImages.splice(index, 1);
        },

        async submitSuggestion() {
            if (!this.validateForm()) {
                return;
            }

            this.isSubmitting = true;

            try {
                const formData = new FormData();
                formData.append('category', this.form.category);
                formData.append('title', this.form.title);
                formData.append('content', this.form.content);
                formData.append('contact', this.form.contact);

                this.uploadedImages.forEach((image, index) => {
                    formData.append(`images[${index}]`, image.file);
                });

                setTimeout(() => {
                    alert('Í±¥ÏùòÏÇ¨Ìï≠Ïù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ï†úÏ∂úÎêòÏóàÏäµÎãàÎã§!');
                    this.resetForm();
                    this.isSubmitting = false;
                }, 1000);

            } catch (error) {
                console.error('Í±¥ÏùòÏÇ¨Ìï≠ Ï†úÏ∂ú Ïã§Ìå®:', error);
                alert('Í±¥ÏùòÏÇ¨Ìï≠ Ï†úÏ∂úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.');
                this.isSubmitting = false;
            }
        },

        validateForm() {
            if (!this.form.category) {
                alert('Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.');
                return false;
            }

            if (!this.form.title.trim()) {
                alert('Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
                return false;
            }

            if (!this.form.content.trim()) {
                alert('ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
                return false;
            }

            return true;
        },

        resetForm() {
            this.form = {
                category: '',
                title: '',
                content: '',
                contact: ''
            };
            this.uploadedImages = [];
            this.$refs.editor.innerHTML = '';

            if (this.$refs.fileInput) {
                this.$refs.fileInput.value = '';
            }
        }
    }
}
</script>

<style scoped>
.suggestion-page {
    min-height: 100vh;
    background: #f8f9fa;
    padding: 2rem 0;
}

.container {
    max-width: 900px;
    margin: 0 auto;
    padding: 0 1rem;
}

/* Ìó§Îçî */
.suggestion-header {
    text-align: center;
    margin-bottom: 3rem;
    margin-top: 60px;
    padding: 2rem 0;
}

.page-title {
    font-size: 2.5rem;
    font-weight: 300;
    color: #212529;
    margin-bottom: 0.5rem;
    letter-spacing: -0.02em;
}

.page-subtitle {
    font-size: 1.1rem;
    color: #6c757d;
    margin: 0;
}

/* Ìèº Ïª®ÌÖåÏù¥ÎÑà */
.suggestion-form-container {
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    padding: 2rem;
    margin-bottom: 2rem;
}

.suggestion-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

/* Ìèº Í∑∏Î£π */
.form-group {
    display: flex;
    flex-direction: column;
}

.form-label {
    font-size: 0.95rem;
    font-weight: 600;
    color: #495057;
    margin-bottom: 0.5rem;
}

.required {
    color: #dc3545;
}

/* ÏûÖÎ†• ÌïÑÎìú */
.form-input,
.form-select {
    width: 100%;
    padding: 0.75rem 1rem;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    font-size: 0.95rem;
    background: #ffffff;
    transition: all 0.2s ease;
}

.form-input:focus,
.form-select:focus {
    outline: none;
    border-color: #6c757d;
    box-shadow: 0 0 0 3px rgba(108, 117, 125, 0.1);
}

.char-count {
    font-size: 0.8rem;
    color: #adb5bd;
    text-align: right;
    margin-top: 0.25rem;
}

.form-help {
    font-size: 0.85rem;
    color: #6c757d;
    margin-top: 0.25rem;
}

.form-help-with-counter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.85rem;
    color: #6c757d;
    margin-top: 0.25rem;
}

/* Î¶¨Ïπò ÌÖçÏä§Ìä∏ ÏóêÎîîÌÑ∞ */
.editor-container {
    border: 1px solid #e9ecef;
    border-radius: 8px;
    overflow: hidden;
    background: #ffffff;
}

.editor-toolbar {
    display: flex;
    align-items: center;
    padding: 0.5rem;
    background: #f8f9fa;
    border-bottom: 1px solid #e9ecef;
    gap: 0.25rem;
}

.toolbar-btn {
    padding: 0.375rem 0.75rem;
    border: 1px solid #e9ecef;
    background: #ffffff;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 0.875rem;
}

.toolbar-btn:hover {
    background: #e9ecef;
}

.toolbar-btn.active {
    background: #6c757d;
    color: white;
    border-color: #6c757d;
}

.toolbar-separator {
    width: 1px;
    height: 24px;
    background: #e9ecef;
    margin: 0 0.5rem;
}

.rich-text-editor {
    min-height: 300px;
    padding: 1rem;
    font-size: 0.95rem;
    line-height: 1.6;
    outline: none;
    background: #ffffff;
}

.rich-text-editor:empty::before {
    content: attr(placeholder);
    color: #adb5bd;
    font-style: italic;
}

.rich-text-editor:focus {
    background: #ffffff;
}

/* Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú */
.image-upload-area {
    margin-top: 0.5rem;
}

.file-input {
    display: none;
}

.upload-zone {
    border: 2px dashed #e9ecef;
    border-radius: 8px;
    padding: 2rem;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s ease;
    background: #fafbfc;
}

.upload-zone:hover {
    border-color: #6c757d;
    background: #f8f9fa;
}

.upload-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.upload-text p {
    font-size: 1rem;
    color: #495057;
    margin: 0 0 0.5rem 0;
}

.upload-text small {
    color: #6c757d;
}

/* Ïù¥ÎØ∏ÏßÄ ÎØ∏Î¶¨Î≥¥Í∏∞ */
.image-preview-container {
    margin-top: 1rem;
}

.image-preview-list {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
}

.image-preview-item {
    position: relative;
    width: 120px;
    height: 120px;
    border-radius: 8px;
    border: 1px solid #e9ecef;
    overflow: hidden;
    background: #f8f9fa;
}

.preview-image {
    width: 100%;
    height: 80px;
    object-fit: cover;
}

.remove-image-btn {
    position: absolute;
    top: 4px;
    right: 4px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: rgba(220, 53, 69, 0.9);
    color: white;
    border: none;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.image-name {
    padding: 0.25rem;
    font-size: 0.8rem;
    color: #495057;
    text-align: center;
    word-break: break-all;
    line-height: 1.2;
    height: 40px;
    overflow: hidden;
}

/* Î≤ÑÌäº */
.form-actions {
    display: flex;
    gap: 1rem;
    justify-content: flex-end;
    padding-top: 1rem;
    border-top: 1px solid #e9ecef;
}

.btn {
    padding: 0.75rem 2rem;
    border-radius: 8px;
    font-size: 0.95rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    border: none;
}

.btn-primary {
    background: #6c757d;
    color: white;
}

.btn-primary:hover:not(:disabled) {
    background: #5a6268;
    transform: translateY(-2px);
}

.btn-primary:disabled {
    background: #adb5bd;
    cursor: not-allowed;
}

.btn-secondary {
    background: #f8f9fa;
    color: #6c757d;
    border: 1px solid #e9ecef;
}

.btn-secondary:hover {
    background: #e9ecef;
    border-color: #6c757d;
}

/* Î∞òÏùëÌòï */
@media (max-width: 768px) {
    .suggestion-page {
        padding: 1rem 0;
    }

    .page-title {
        font-size: 2rem;
    }

    .suggestion-form-container {
        padding: 1.5rem;
    }

    .form-actions {
        flex-direction: column;
    }

    .btn {
        width: 100%;
    }

    .image-preview-list {
        justify-content: center;
    }

    .editor-toolbar {
        flex-wrap: wrap;
    }
}

/* Ï†ëÍ∑ºÏÑ± */
.form-input::placeholder,
.form-select::placeholder {
    color: #adb5bd;
}

/* Î°úÎî© ÏÉÅÌÉú */
.btn:disabled {
    opacity: 0.6;
}
</style>
